body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


#menu-list{
    font-size:1.2em;
    color:white;
    font-weight:bold;
    float: right;
    text-align: center
}

#logo{
    font-size:1.4em;
    color:white;
    font-weight:bold
}

.menu-item-logout:hover{
    cursor: pointer;
    color:lightgrey
}

@media (max-width: 480px) {
    #menu-list{
        font-size:0.8em;
    
    }
    #logo{
        font-size:1.2em;
    }
}

@media (max-width: 320px) {
    #menu-list{
        font-size:0.7em;
    }
    #logo{
        font-size:1em;
    }
}
.addToDo-field{
    width: 200px !important;
}

@media (max-width: 480px) {
    .addToDo-field{
        width: 50% !important;
    }
}
.todo-list{
    width: 50%;
    margin-top: 1rem;
}

@media (max-width: 480px) {
    .todo-list{
        width: 100%;
        margin-top: 1rem;
    }
}
* {
    box-sizing: border-box;
}

.auth-container {
    opacity: 0.3
}

.auth-background {
    background: -webkit-linear-gradient(top, rgb(11, 102, 189) 40%, rgba(228, 225, 225, 0.877) 0%);
    background: linear-gradient(-180deg, rgb(11, 102, 189) 40%, rgba(228, 225, 225, 0.877) 0%);
    height:100%;
    width: 100%;
    position: fixed;
}

.logo {
    margin: 0;
    position: absolute;
    display: block;
    top: 10%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: "Fjalla One", sans-serif;
    font-size: 2.5rem;
    line-height: 40px;
    color:white;
    text-align: center
}

.login-form {
    margin: 0;
    padding: 40px;
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    width: 480px;
    height: auto;
    border-radius: 5px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
    color: black;
    font-size: 18px;
    vertical-align: baseline;
}

.welcome {
    margin-bottom: 22px;
    font-size: 40px;
    font-weight: 400;
    color: black;
    text-align: center;
}

.login-field {
    width: 100%;
    padding: 0;
}

.login-field-name {
    margin-bottom: 8px;
    padding: 0;
    line-height: 16px;
    color:black;
}

.login-input {
    height: 40px;
    font-size: 16px;
}

.login-btn {
    margin: 0;
}

.login-form-button {
    width: 100%;
    height: 40px;
    font-size: 16px;
    margin-top: 16px;
    margin-bottom: 8px;
    background-color: rgb(11, 102, 189);
    border: none;
}

.login-form-button:hover {
    background-color: #677bc4;
}

.login-form-button:focus {
    background-color: #677bc4;
}

.register {
    font-size: 14px;
    line-height: 18px;
}

.login-link {
    color: #7289da;
}

.login-link:hover {
    cursor: pointer;
}

.login-link:hover {
    color: #677bc4;
}

.login-logo h1 {
    text-align: center;
    color: rgb(7, 6, 6);
}

.register-form {
    margin: 0;
    margin-top: 44px;
    padding: 40px;
    position: absolute;
    display: block;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: white;
    width: 480px;
    height: auto;
    border-radius: 5px;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.2);
    color: black;
    font-size: 18px;
    vertical-align: baseline;
}

@media (max-width: 480px) {
    .auth-background {
        background: -webkit-linear-gradient(top, rgb(11, 102, 189) 20%, rgba(228, 225, 225, 0.877) 0%);
        background: linear-gradient(-180deg, rgb(11, 102, 189) 20%, rgba(228, 225, 225, 0.877) 0%);
        height:100%;
        width: 100%;
        position: fixed;
    }

    .register-form {
        top: 50%;
        width: 100%;
        height: auto;
        background-color: transparent;
        box-shadow: none;
        -webkit-box-shadow: none;
    }

    .login-form {
        top: 50%;
        width: 100%;
        height: auto;
        background-color: transparent;
        box-shadow: none;
        -webkit-box-shadow: none;
    }

    .logo {
        margin: 0;
        position: absolute;
        display: block;
        top: 10%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        font-family: "Fjalla One", sans-serif;
        font-size: 2rem;
        line-height: 40px;
        color:white;
        text-align: center
    }
}
.loader {
    width: 64px;
    height: 64px;
    position: absolute;
    top: 40%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.loader div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 71px;
    height: 71px;
    margin: 6px;
    border: 5px solid rgb(11, 102, 189);
    border-radius: 50%;
    -webkit-animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(11, 102, 189) transparent transparent transparent;
}
.loader div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
}
.loader div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
}
.loader div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
}
@-webkit-keyframes loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes loader {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

